.main-shopping-list {
  gap: 40px;
  padding-top: 40px;
  display: flex;
}

@media screen and (min-width: 375px) {
  .container-shopping-list {
    max-width: 375px;
    margin: 0 auto;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container-shopping-list {
    max-width: none;
    width: 768px;
    padding: 0 32px;
  }
}

.violet-color {
  color: #4f2ee8;
}

.shopping-main__title {
  margin-bottom: 40px;
  margin-left: 20px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
}

.empty-list {
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  display: flex;
}

.shopping-empty-text {
  text-align: center;
  max-width: 349px;
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 1.33;
}

.shopping-empty-img {
  width: 265px;
  height: 198px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.shopping-list__page {
  width: 100%;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media screen and (min-width: 768px) {
  .shopping-empty-img {
    width: 322px;
    height: 241px;
  }

  .shopping-main__title {
    font-size: 48px;
    line-height: 1.08;
  }
}

.shopping-button {
  width: 34px;
  height: 34px;
  position: absolute;
  top: 24px;
  right: 24px;
}

.book-in-shopping-list {
  position: relative;
}

.shopping-list__item {
  max-width: 335px;
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  margin-bottom: 20px;
  padding: 20px;
  position: relative;
}

.shopping-list__img {
  max-width: 98px;
  max-height: 139px;
  height: auto;
  flex-shrink: 0;
  margin-bottom: 4px;
  margin-right: 20px;
  display: block;
}

.shopping-list__title {
  max-width: 144px;
  margin-bottom: 4px;
  margin-left: 128px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.12;
}

